import {useState, useRef, useEffect} from 'react';
import {Select} from '@rmwc/select'
import '@rmwc/select/styles'
import { getAuth, onAuthStateChanged, signInWithEmailAndPassword, setPersistence, browserSessionPersistence  } from 'firebase/auth';
import '@rmwc/textfield/styles';
import { TextField } from "@rmwc/textfield";
import '@rmwc/icon/styles';
import {Typography} from '@rmwc/typography'
import '@rmwc/typography/styles';
import {Button} from '@rmwc/button'
import '@rmwc/button/styles';

const app = initializeApp({
    apiKey: "AIzaSyDkL9MVC5911Smi58cwo9HBs7-rJjYd88w",
    authDomain: "remote-lighting.firebaseapp.com",
    databaseURL: "https://remote-lighting-default-rtdb.firebaseio.com",
    projectId: "remote-lighting",
    storageBucket: "remote-lighting.appspot.com",
    messagingSenderId: "291955304057",
    appId: "1:291955304057:web:ebfffb63277604dd8f16a8",
    measurementId: "G-BFP5RRMLHL"
  });

const db = getDatabase(), dbRef = ref(db), auth = getAuth(app);


function Terminal (){
    const [lights, setLights] = useState({});
    setPersistence(auth, browserSessionPersistence)
    async function handler(){
        await signInWithEmailAndPassword(auth, 'lingjiati@gmail.com', value)
        setAuthed(true)
    }
    const [authed, setAuthed] = useState(true)
    const [value, setValue] = useState('')
    useEffect(() => get(dbRef).then((a) => setLights(JSON.parse(JSON.stringify(a)))), [])
    useEffect(() => update(dbRef, lights), [lights])
    var array = [];
    for(var i in lights) array[i] = lights[i];
    
    return (
    <div style={{ display: 'flex', width: '100%', flexDirection: 'column', flexWrap: 'wrap', 'justifyContent': 'space-between'}}>
        <Typography use="headline6" style={{margin: '32px 32px 8px'}}>Remote Lighting Terminal</Typography>
        {!authed?
        
        <TextField onChange={evt => setValue(evt.currentTarget.value)} value={value} 
            trailingIcon={{
            icon: 'login',
            tabIndex: 0,
            
            onClick: handler }} label="Password" style={{margin: '8px 32px'}}/>
        
        :
        <>
        {
        array.map((a, b) => <Select
            label={"Channel " + (b+1)}
            enhanced
            value={a}
            options={['White', 'Grey', 'Dark Grey', 'Black', 'Blue', "Red"]}
            key={b}
            onChange={evt => {
                var val = {...lights};
                val[b] = evt.currentTarget.value;
                setLights(val)
            }}
            
        />)
        
        }
        <Button label="Add" icon="add" style={{alignSelf: 'flex-start', margin: '8px 32px'}} onClick={() => {
                const length = array.length;
                var val = {...array};
                val[length] = "White";
                console.log(val)
                setLights(val)
            }}/>
        </>}
    </div>)
    }

export default Terminal